body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
}

.modal-main {
  position:fixed;
  width: 80%;
  height: 500px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

.close:hover {
  -webkit-animation: animate 8s linear infinite;
          animation: animate 8s linear infinite; 
  -webkit-transform: scale(1.5) perspective(1px); 
          transform: scale(1.5) perspective(1px);
}

.plus:hover {
  -webkit-animation: animate 8s linear infinite;
          animation: animate 8s linear infinite;
  -webkit-transform: scale(1.5) perspective(1px);
          transform: scale(1.5) perspective(1px);
}

.entList:hover {
  border-radius: 50% 50% 0% 0% / 5% 5% 0% 0%;
  box-shadow: 10px 10px rgba(0,0,0,.50);
}

.appList:hover {
  border-radius: 50% 50% 0% 0% / 5% 5% 0% 0%;
  box-shadow: 10px 10px rgba(0,0,0,.50);
}

.furnList:hover {
  border-radius: 50% 50% 0% 0% / 5% 5% 0% 0%;
  box-shadow: 10px 10px rgba(0,0,0,.50);
}

.ReactModal__Overlay {
  opacity: 0;
  -webkit-transition: opacity 2000ms ease-in-out;
  transition: opacity 2000ms ease-in-out;
}

.ReactModal__Overlay--after-open{
  opacity: 1;
}

.ReactModal__Overlay--before-close{
  opacity: 0;
}





@-webkit-keyframes animate {
  0% {
    background-position: 0%;
  }
  100% {
    background-position: 300%;
  }
}





@keyframes animate {
  0% {
    background-position: 0%;
  }
  100% {
    background-position: 300%;
  }
}

body {
    background: url('https://img.freepik.com/free-photo/dark-room-with-wooden-floor_1048-2481.jpg?size=626&ext=jpg');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-attachment: fixed;
}
